.full-time {
  position: relative;
  display: flex;
  align-items: flex-start;
  width: 1200px;
  margin-left: 0;
  min-height: 643px;
  opacity: 0.5;
  z-index: 0;
}


.full-time__wrapper {
  position: relative;
  width: 570px;
  display: flex;
  flex-direction: column;
  margin-top: 15px;
  margin-left: 575px;
  border-radius: 20px;
  background-color: @white;
  box-shadow: 0 20px 31px rgba(0, 0, 0, 0.1);
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 132px;
    width: 307px;
    height: 36px;
    background-image: url("../img/allips.png");
    background-repeat: no-repeat;
  }
}

.full-time__title {
  margin: 0;
  margin-top: 65px;
  padding: 0 20px;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
}

.full-time__text {
  margin: 0;
  margin-top: 25px;
  padding-left: 47px;
  padding-right: 70px;
  width: 570px;
}

.full-time__list-name {
  display: block;
  margin-top: 20px;
  padding-left: 47px;
  padding-right: 70px;
  text-align: left;
  font-family: "Geometria", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  color: @red-2;
}

.full-time__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-left: 47px;
  padding-right: 70px;
}

.full-time__item-text {
  margin: 0;
}

.full-time__text-red {
  color: @red-2;
}

.full-time__schedule {
  list-style: none;
  margin: 0;
  margin-top: 25px;
  padding: 0;
  padding-left: 47px;
  padding-right: 70px;
}

.full-time__shedule-text {
  margin: 0;
  font-family: "Geometria", "Arial", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}

.full-time__list-term {
  margin: 0;
  padding-right: 70px;
  padding-left: 47px;
  color: @red-2;
}

.full-time__text-time {
  margin: 0;
  margin-bottom: 40px;
  padding-left: 47px;
  padding-right: 70px;
}

.full-time__image {
  position: absolute;
  left: 0;
  bottom: 0;
  margin-left: 234px;
  z-index: 2;
}
