.slider {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  margin-top: 38px;
  padding: 0;
  list-style: none;
}

.slider__item {
  margin: 0 auto;
}

.slider__wrapper {
  display: flex;
  flex-direction: column;
  width: 570px;
  margin: 0 auto;
  align-items: center;
  background-color: @white;
  box-shadow: 0 20px 31px rgba(0, 0, 0, 0.1);
  border-radius: 30px;
  position: relative;
  z-index: 1;
}

.slider__title {
  width: 570px;
  margin: 0;
  text-align: center;
  margin-top: 30px;
  padding: 0 20px;
  font-family: "Geometria-extra", "Arial", sans-serif;
  font-size: 20px;
  line-height: 23px;
  text-transform: uppercase;

  &--5 {
    margin-top: 20px;
    width: 450px;
    display: block;
    font-size: 15px;
  }

  &--red {
    font-size: 20px;
    color: @red-1;
  }
}

.slider__text {
  margin: 0;
  margin-top: 20px;
  padding: 0 20px;
  width: 492px;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
}

.slider__button {
  display: block;
  position: relative;
  width: 230px;
  margin: 0 auto;
  margin-top: 35px;
  margin-bottom: 28px;
  padding: 11px 20px;
  text-align: center;
  border: none;
  border-radius: 20px;
  background-color: @red-1;
  box-shadow: 0 0 25px rgba(187, 16, 3, 0.4);
  color: @white;
  text-decoration: none;
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;

  &::after {
    content: "";
    position: absolute;
    top: 13px;
    left: 250px;
    width: 30px;
    height: 15px;
    background-image: url("../img/arrow.png");
    background-repeat: no-repeat;
    background-size: 30px auto;
  }

  &--4 {
    margin-top: 17px;
  }

  &--5 {
    margin-top: 23px;

    &::after {
      display: none;
    }
  }
}

.slider__car {
  position: relative;
  width: 1920px;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    height: 55px;
    background-repeat: no-repeat;
  }

  &--1 {
    &::before {
      width: 717px;
      background-image: url("../img/tire-1.png");
    }
  }

  &--2 {
    &::before {
      width: 862px;
      background-image: url("../img/tire-2.png");
    }
  }

  &--3 {
    &::before {
      width: 1168px;
      background-image: url("../img/tire-3.png");
    }
  }

  &--4 {
    &::before {
      width: 1468px;
      background-image: url("../img/tire-4.png");
    }
  }

  &--5 {
    &::before {
      width: 1785px;
      background-image: url("../img/tire-5.png");
    }
  }
}

.slider__image {
  position: relative;
  display: block;
  margin-top: -25px;

  &--1 {
    margin-left: 164px;
  }

  &--2 {
    margin-left: 324px;
  }

  &--3 {
    margin-left: 624px;
  }

  &--4 {
    margin-left: 922px;
  }

  &--5 {
    margin-left: 1228px;
  }
}

.slider__form-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-top: 20px;
  padding: 0;
  width: 570px;
  list-style: none;
}

.slider__form-item {
  padding: 0 30px;

  &:nth-child(2n) {
    padding-left: 0;
  }
}

.slider__input {
  width: 240px;
  border: 1px solid @gray;
  font-family: "Geometria", "Arial", sans-serif;
  font-size: 14px;
  line-height: 23px;
  padding: 4px 21px;
  color: @black-2;

  &::placeholder {
    color: @gray-2;
  }
}
