*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 20px;
  font-family: "Geometria", "Arial", sans-serif;
  color: #1e1e1e;
  font-style: normal;
  font-weight: normal;
}
img {
  max-width: 100%;
  height: auto;
}
.hidden {
  display: none;
}
@font-face {
  font-family: "Geometria";
  src: url("../fonts/geometriaregular.woff2") format("woff2"), url("../fonts/geometriaregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Geometria";
  src: url("../fonts/geometriabold.woff2") format("woff2"), url("../fonts/geometriabold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Geometria-heavy";
  src: url("../fonts/geometriaheavy.woff2") format("woff2"), url("../fonts/geometriaheavy.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Geometria-extra";
  src: url("../fonts/geometriaextrabold.woff2") format("woff2"), url("../fonts/geometriaextrabold.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.offer {
  position: relative;
  width: 1920px;
  min-height: 705px;
  margin: 0 auto;
  background-image: url("../img/background-1.png");
  background-size: 1750px auto;
  background-repeat: no-repeat;
  z-index: 0;
}
.offer::after {
  content: "";
  position: absolute;
  top: 543px;
  width: 1920px;
  height: 168px;
  background-image: url("../img/road.jpg");
  background-repeat: no-repeat;
  z-index: -1;
}
.offer::before {
  content: "";
  position: absolute;
  top: 331px;
  right: 0;
  width: 167px;
  height: 379px;
  background-image: url("../img/finish.png");
  background-repeat: no-repeat;
}
.offer__text {
  margin: 0 auto;
  padding-top: 123px;
  text-align: center;
  width: 541px;
  font-family: "Geometria-heavy", "Arial", sans-serif;
  font-size: 30px;
  line-height: 20px;
}
.offer__text-red {
  display: block;
  margin-top: 17px;
  color: #bb1003;
  text-transform: uppercase;
}
.slider {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  margin-top: 38px;
  padding: 0;
  list-style: none;
}
.slider__item {
  margin: 0 auto;
}
.slider__wrapper {
  display: flex;
  flex-direction: column;
  width: 570px;
  margin: 0 auto;
  align-items: center;
  background-color: #ffffff;
  box-shadow: 0 20px 31px rgba(0, 0, 0, 0.1);
  border-radius: 30px;
  position: relative;
  z-index: 1;
}
.slider__title {
  width: 570px;
  margin: 0;
  text-align: center;
  margin-top: 30px;
  padding: 0 20px;
  font-family: "Geometria-extra", "Arial", sans-serif;
  font-size: 20px;
  line-height: 23px;
  text-transform: uppercase;
}
.slider__title--5 {
  margin-top: 20px;
  width: 450px;
  display: block;
  font-size: 15px;
}
.slider__title--red {
  font-size: 20px;
  color: #bb1003;
}
.slider__text {
  margin: 0;
  margin-top: 20px;
  padding: 0 20px;
  width: 492px;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
}
.slider__button {
  display: block;
  position: relative;
  width: 230px;
  margin: 0 auto;
  margin-top: 35px;
  margin-bottom: 28px;
  padding: 11px 20px;
  text-align: center;
  border: none;
  border-radius: 20px;
  background-color: #bb1003;
  box-shadow: 0 0 25px rgba(187, 16, 3, 0.4);
  color: #ffffff;
  text-decoration: none;
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;
}
.slider__button::after {
  content: "";
  position: absolute;
  top: 13px;
  left: 250px;
  width: 30px;
  height: 15px;
  background-image: url("../img/arrow.png");
  background-repeat: no-repeat;
  background-size: 30px auto;
}
.slider__button--4 {
  margin-top: 17px;
}
.slider__button--5 {
  margin-top: 23px;
}
.slider__button--5::after {
  display: none;
}
.slider__car {
  position: relative;
  width: 1920px;
}
.slider__car::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 55px;
  background-repeat: no-repeat;
}
.slider__car--1::before {
  width: 717px;
  background-image: url("../img/tire-1.png");
}
.slider__car--2::before {
  width: 862px;
  background-image: url("../img/tire-2.png");
}
.slider__car--3::before {
  width: 1168px;
  background-image: url("../img/tire-3.png");
}
.slider__car--4::before {
  width: 1468px;
  background-image: url("../img/tire-4.png");
}
.slider__car--5::before {
  width: 1785px;
  background-image: url("../img/tire-5.png");
}
.slider__image {
  position: relative;
  display: block;
  margin-top: -25px;
}
.slider__image--1 {
  margin-left: 164px;
}
.slider__image--2 {
  margin-left: 324px;
}
.slider__image--3 {
  margin-left: 624px;
}
.slider__image--4 {
  margin-left: 922px;
}
.slider__image--5 {
  margin-left: 1228px;
}
.slider__form-list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  margin-top: 20px;
  padding: 0;
  width: 570px;
  list-style: none;
}
.slider__form-item {
  padding: 0 30px;
}
.slider__form-item:nth-child(2n) {
  padding-left: 0;
}
.slider__input {
  width: 240px;
  border: 1px solid #e8e8e8;
  font-family: "Geometria", "Arial", sans-serif;
  font-size: 14px;
  line-height: 23px;
  padding: 4px 21px;
  color: #191919;
}
.slider__input::placeholder {
  color: #898989;
}
.format {
  position: relative;
  margin: 0 auto;
  width: 1920px;
  background-image: url("../img/background-2.jpg");
  background-size: 1920px auto;
  background-repeat: no-repeat;
}
.format__text {
  margin: 0;
  padding: 0 20px;
  padding-top: 60px;
  font-family: "Geometria-heavy", "Arial", sans-serif;
  font-size: 30px;
  line-height: 20px;
  text-align: center;
}
.format__text-red {
  color: #bb1003;
}
.format__study {
  display: flex;
  flex-wrap: wrap;
  margin-top: 35px;
}
.format__buttons {
  display: flex;
  margin: 0 auto;
  margin-top: -60px;
  z-index: 2;
}
.format__button {
  border: none;
  background-color: transparent;
  padding: 0;
  display: block;
  width: 35px;
  height: 35px;
  cursor: pointer;
  outline: none;
}
.format__button--left {
  margin-right: 15px;
}
.format__button:active .format__left-button--stroke {
  stroke: #db0701;
}
.format__button:active .format__left-button--fill {
  fill: #db0701;
}
.distance {
  position: relative;
  display: flex;
  align-items: flex-start;
  width: 720px;
  margin-left: 0;
  min-height: 643px;
  z-index: 1;
}
.distance__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 115px;
  margin-left: -420px;
  border-radius: 20px;
  background-color: #ffffff;
  box-shadow: 0 20px 31px rgba(0, 0, 0, 0.1);
  z-index: 1;
}
.distance__wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 132px;
  width: 307px;
  height: 36px;
  background-image: url("../img/allips.png");
  background-repeat: no-repeat;
}
.distance__title {
  margin: 0;
  margin-top: 65px;
  padding: 0 20px;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
}
.distance__text {
  margin: 0;
  margin-top: 25px;
  padding-left: 47px;
  padding-right: 70px;
  width: 570px;
}
.distance__list-name {
  display: block;
  margin-top: 20px;
  padding-left: 47px;
  padding-right: 70px;
  text-align: left;
  font-family: "Geometria", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  color: #db0701;
}
.distance__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-left: 47px;
  padding-right: 70px;
}
.distance__item-text {
  margin: 0;
}
.distance__text-red {
  color: #db0701;
}
.distance__online {
  display: block;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  margin: 20px 0 25px;
  padding-left: 47px;
  padding-right: 70px;
  text-transform: uppercase;
}
.distance__image {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-right: 124px;
  z-index: 2;
}
.full-time {
  position: relative;
  display: flex;
  align-items: flex-start;
  width: 1200px;
  margin-left: 0;
  min-height: 643px;
  opacity: 0.5;
  z-index: 0;
}
.full-time__wrapper {
  position: relative;
  width: 570px;
  display: flex;
  flex-direction: column;
  margin-top: 15px;
  margin-left: 575px;
  border-radius: 20px;
  background-color: #ffffff;
  box-shadow: 0 20px 31px rgba(0, 0, 0, 0.1);
  z-index: 1;
}
.full-time__wrapper::before {
  content: "";
  position: absolute;
  top: 0;
  left: 132px;
  width: 307px;
  height: 36px;
  background-image: url("../img/allips.png");
  background-repeat: no-repeat;
}
.full-time__title {
  margin: 0;
  margin-top: 65px;
  padding: 0 20px;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
}
.full-time__text {
  margin: 0;
  margin-top: 25px;
  padding-left: 47px;
  padding-right: 70px;
  width: 570px;
}
.full-time__list-name {
  display: block;
  margin-top: 20px;
  padding-left: 47px;
  padding-right: 70px;
  text-align: left;
  font-family: "Geometria", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  color: #db0701;
}
.full-time__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-left: 47px;
  padding-right: 70px;
}
.full-time__item-text {
  margin: 0;
}
.full-time__text-red {
  color: #db0701;
}
.full-time__schedule {
  list-style: none;
  margin: 0;
  margin-top: 25px;
  padding: 0;
  padding-left: 47px;
  padding-right: 70px;
}
.full-time__shedule-text {
  margin: 0;
  font-family: "Geometria", "Arial", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}
.full-time__list-term {
  margin: 0;
  padding-right: 70px;
  padding-left: 47px;
  color: #db0701;
}
.full-time__text-time {
  margin: 0;
  margin-bottom: 40px;
  padding-left: 47px;
  padding-right: 70px;
}
.full-time__image {
  position: absolute;
  left: 0;
  bottom: 0;
  margin-left: 234px;
  z-index: 2;
}
