.format {
  position: relative;
  margin: 0 auto;
  width: 1920px;
  background-image: url("../img/background-2.jpg");
  background-size: 1920px auto;
  background-repeat: no-repeat;
}

.format__text {
  margin: 0;
  padding: 0 20px;
  padding-top: 60px;
  font-family: "Geometria-heavy", "Arial", sans-serif;
  font-size: 30px;
  line-height: 20px;
  text-align: center;
}

.format__text-red {
  color: @red-1;
}

.format__study {
  display: flex;
  flex-wrap: wrap;
  margin-top: 35px;
}

.format__buttons {
  display: flex;
  margin: 0 auto;
  margin-top: -60px;
  z-index: 2;
}

.format__button {
  border: none;
  background-color: transparent;
  padding: 0;
  display: block;
  width: 35px;
  height: 35px;
  cursor: pointer;
  outline: none;

  &--left {
    margin-right: 15px;
  }
}

.format__button:active .format__left-button--stroke {
  stroke: @red-2;
}

.format__button:active .format__left-button--fill {
  fill: @red-2;
}
