.offer {
  position: relative;
  width: 1920px;
  min-height: 705px;
  margin: 0 auto;
  background-image: url("../img/background-1.png");
  background-size: 1750px auto;
  background-repeat: no-repeat;
  z-index: 0;

  &::after {
    content: "";
    position: absolute;
    top: 543px;
    width: 1920px;
    height: 168px;
    background-image: url("../img/road.jpg");
    background-repeat: no-repeat;
    z-index: -1;
  }

  &::before {
    content: "";
    position: absolute;
    top: 331px;
    right: 0;
    width: 167px;
    height: 379px;
    background-image: url("../img/finish.png");
    background-repeat: no-repeat;
  }
}

.offer__text {
  margin: 0 auto;
  padding-top: 123px;
  text-align: center;
  width: 541px;
  font-family: "Geometria-heavy", "Arial", sans-serif;
  font-size: 30px;
  line-height: 20px;
}

.offer__text-red {
  display: block;
  margin-top: 17px;
  color: @red-1;
  text-transform: uppercase;
}
