*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 20px;
  font-family: "Geometria", "Arial", sans-serif;
  color: @black-1;
  font-style: normal;
  font-weight: normal;
}

img {
  max-width: 100%;
  height: auto;
}

.hidden {
  display: none;
}
