.distance {
  position: relative;
  display: flex;
  align-items: flex-start;
  width: 720px;
  margin-left: 0;
  min-height: 643px;
  z-index: 1;
}

.distance__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin-top: 115px;
  margin-left: -420px;
  border-radius: 20px;
  background-color: @white;
  box-shadow: 0 20px 31px rgba(0, 0, 0, 0.1);
  z-index: 1;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 132px;
    width: 307px;
    height: 36px;
    background-image: url("../img/allips.png");
    background-repeat: no-repeat;
  }
}

.distance__title {
  margin: 0;
  margin-top: 65px;
  padding: 0 20px;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
}

.distance__text {
  margin: 0;
  margin-top: 25px;
  padding-left: 47px;
  padding-right: 70px;
  width: 570px;
}

.distance__list-name {
  display: block;
  margin-top: 20px;
  padding-left: 47px;
  padding-right: 70px;
  text-align: left;
  font-family: "Geometria", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  color: @red-2;
}

.distance__list {
  list-style: none;
  margin: 0;
  padding: 0;
  padding-left: 47px;
  padding-right: 70px;
}

.distance__item-text {
  margin: 0;
}

.distance__text-red {
  color: @red-2;
}

.distance__online {
  display: block;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  margin: 20px 0 25px;
  padding-left: 47px;
  padding-right: 70px;
  text-transform: uppercase;
}

.distance__image {
  position: absolute;
  right: 0;
  bottom: 0;
  margin-right: 124px;
  z-index: 2;
}
